.category-container {
    width: 99%;
    margin: 20px auto;
}
 
.category-container .category-header {
    width: 100%;
    height: 7rem;
    background-color: #fff;
    display: flex;
    align-items: center;
}

.category-container .category-header form {
    width: 100%;
}

.category-container .category-header form .label {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 20px;
    background-color: rgb(243,246,249);
    margin:0 20px ;
    height: 4rem;
    align-items: center;
    border-radius: 4px;
}

.category-container .category-header form .label input {
    width: 100%;
    height: 2rem;
    margin-right: 10px;
    border-radius: 4px;
    border: 1px solid rgb(206,212,218);
    outline: none;
    padding-left: 15px;
}

.category-container .category-header form .label button {
    width: 12.7rem;
    background: rgb(64,81,137);
    border-radius: 4px;
    border: 1px solid rgb(64,81,137);
    color: #fff;
    height: 2rem;
}

.category-container .category-body {
    width: 100%;
    margin-top: 5px;
    padding: 20px 0;
    background-color: #fff;
}