.category-container {
  width: 99%;
  margin: 20px auto;
}

.category-container .category-body {
  width: 100%;
  margin-top: 5px;
  padding: 20px 0;
  background-color: #fff;
}

.category-container .category-body .pagination {
  width: 100%;
  margin: 20px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.category-container .category-body .search-section {
  width: 100%;
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.category-container .category-body .search-section form {
  width: 90%;
}

.category-container .category-body .search-section form input {
  height: 2rem;
  padding-left: 10px;
  border: 1px solid rgb(183, 189, 194);
  margin-top: 5px;
  color: rgb(0, 0, 0);
  border-radius: 4px;
  outline: none;
  width: calc(100% - 6rem);
}

.category-container .category-body .search-section form button {
  padding: 7px 15px;
  background: rgb(64, 81, 137);
  border-radius: 4px;
  border: 1px solid rgb(64, 81, 137);
  color: #fff;
  font-weight: 500;
  font-size: 14px;
}
