.dashboard-container {
  width: 98%;
  margin: 50px auto;
}

.dashboard-container .general {
  width: 95%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0 auto;
}

.dashboard-container .general .card {
  width: 32%;
  height: 12rem;
  background-color: #fff;
  border-radius: 6px;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
}

.dashboard-container .general .card .card-header {
  color: rgb(101, 116, 139);
  font-size: 14px;
  height: 2.5rem;
  display: flex;
  align-items: center;
  margin-left: 20px;
}

.dashboard-container .general .card .card-body {
  width: 100%;
  height: 9.5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 28px;
  color: rgb(18, 24, 40);
}

.dashboard-container .diagrams {
  width: 98%;
  margin: 20px auto;
  background-color: #fff;
  border-radius: 6px;
  display: flex;
  justify-content: center;
  padding: 5px;
}

@media screen and (max-width: 768px) {
  .dashboard-container .general {
    width: 95%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 0 auto;
  }

  .dashboard-container .general .card {
    width: 97%;
    margin: 10px auto;
  }
}
