.profile-container {
    width: 98%;
    margin: 20px auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.profile-container .left-section {
    width: 30%;
    height: 20rem;
    background-color: #fff;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 15px;
}

.profile-container .left-section .image {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    overflow: hidden;
    border: 3px solid rgb(64, 81, 137);
}

.profile-container .left-section .image img {
    width: 150px;
    min-height: 150px;
}

.profile-container .left-section .info {
    margin-top: 25px;
    text-align: center;
}

.profile-container .left-section .info .name {
    color: rgb(73,80,87);
    font-size: 16px;
    font-weight: 600;
}

.profile-container .left-section .info .role {
    color: rgb(156,148,187);
    font-size: 14px;
}


.profile-container .right-section {
    width: 69%;
    background-color: #fff;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    margin-left: 10px;
}

.profile-container .right-section .right-section-header {
    width: 100%;
    height: 3rem;
    border-bottom: 1px solid rgb(233,235,236);
    display: flex;
    align-items: center;
} 

.profile-container .right-section .right-section-header .right-section-control {
    padding:0 20px;
    height: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.profile-container .right-section .right-section-header .active {
    border-bottom: 1px solid rgb(70,84,160);
}

.profile-container .right-section .right-section-header .right-section-control button {
    background: transparent;
    outline: none;
    border: none;
    font-size: 13px;
    font-weight: 600;
    color: rgb(70,84,160);
}

.profile-container .right-section .right-section-details{
    padding:0 30px;
}

.profile-container .right-section .right-section-details form {
    width: 100%;
}

.profile-container .right-section .right-section-details form .detail {
    display: flex;
    flex-direction: column;
    padding-top: 20px;

}

.profile-container .right-section .right-section-details form .detail label {
    color: rgba(47, 37, 41, 0.792);
    font-weight: 600;
    font-size: 12px;
}

.profile-container .right-section .right-section-details form .detail input {
   height: 2rem;
   padding-left: 10px;
   border: 1px solid rgb(183, 189, 194);
   margin-top: 5px;
   color: rgb(0, 0, 0);
   border-radius: 4px;
   outline: none;
}

.profile-container .right-section .right-section-details form .update-button {
    margin: 20px 0;
}

.profile-container .right-section .right-section-details form .update-button button {
    padding: 6px 15px;
    background: rgb(64,81,137);
    border-radius: 4px;
    border: 1px solid rgb(64,81,137);
    color: #fff;
    font-weight: 500;
    font-size: 14px;
}

@media screen and (max-width: 1024px) {
    .profile-container {
        width: 95%;
        flex-direction: column;
    }

    .profile-container .left-section {
        margin-bottom: 20px;
    }

    .profile-container .left-section, .profile-container .right-section {
        width: 100%;
    }
    .profile-container .right-section {
        margin-top: 20px;
    }
}