.cashier-container {
    width: 98%;
    margin: 20px auto;
    background-color: #fff;
}

.cashier-container .search-section {
    width: 100%;
    padding: 20px 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
 
.cashier-container .search-section form {
    width: 90%;
}

.cashier-container .search-section form input {
    height: 2rem;
    padding-left: 10px;
    border: 1px solid rgb(183, 189, 194);
    margin-top: 5px;
    color: rgb(0, 0, 0);
    border-radius: 4px;
    outline: none;
    width: calc(100% - 6rem);
 }

 .cashier-container .search-section form button {
    padding: 7px 15px;
    background: rgb(64,81,137);
    border-radius: 4px;
    border: 1px solid rgb(64,81,137);
    color: #fff;
    font-weight: 500;
    font-size: 14px;
}

.cashier-container .paid-container {
    width: 100%;
}

.cashier-container .paid-container form {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.cashier-container .paid-container form div .label label {
    margin-right: 7px;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.713);
}

.cashier-container .paid-container form button {
    padding: 10px 10px;
    background: rgb(64,81,137);
    border: none;
    border-radius: 4px;
    color: #fff;
    font-weight: 500;
}

.cashier-container .paid-container form  button[disabled=disabled],.cashier-container .paid-container form  button:disabled {
    opacity: 0.6;
    cursor: not-allowed;
}