.doctor-container .right-section {
    width: 98%;
    background-color: #fff;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    margin: 20px auto;
    padding-bottom: 10px;
}

.doctor-container .right-section .right-section-header {
    width: 100%;
    height: 3rem;
    border-bottom: 1px solid rgb(233,235,236);
    display: flex;
    align-items: center;
} 

.doctor-container .right-section .right-section-header .right-section-control {
    padding: 0 20px;
    height: 3rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.doctor-container .right-section .right-section-header .active {
    border-bottom: 1px solid rgb(70,84,160);
}

.doctor-container .right-section .right-section-header .right-section-control button {
    background: transparent;
    outline: none;
    border: none;
    font-size: 13px;
    font-weight: 600;
    color: rgb(70,84,160);
}

.doctor-container .right-section .control-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 4rem;
    align-items: center;
}

.doctor-container .right-section .control-container p {
    margin-left: 10px;
    font-size: 14px;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.9);
}

.doctor-container .right-section .control-container button {
    padding: 9px 10px;
    margin-right: 10px;
    font-size: 14px;
    font-weight: 500;
    border: none;
    color: #fff;
    background-color: rgb(70,84,160);
    border-radius: 4px;
}